.my-works {
  position: relative;
}

.my-works__list {
  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 271px 271px;
    column-gap: 3.53rem;
    margin-left: 3.12rem;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: 245px 245px 245px;
    column-gap: 4rem;
    margin-left: 12.2rem;
  }
}

.my-works__item {
  margin-bottom: 1.88rem;

  @media (min-width: $tablet-width) {
    margin-bottom: 2.8rem;
  }

  @media (min-width: $desktop-width) {
    position: relative;

    &:hover > .my-works__text-about {
      display: block;
    }
  }
}

.my-works__text {
  margin-left: 0.3em;
  margin-bottom: 1em;

  text-align: left;

  @media (min-width: $tablet-width) {
    margin-bottom: 1em;
    margin-left: 0;
  }

  @media (min-width: $desktop-width) {
    font-size: 0.88rem;
    line-height: 1.33em;
  }
}

.my-works__link {
  &:hover > .my-works__img-wrap {
    animation-name: background-pos;
    animation-duration: 6s;
  }
}

@keyframes background-pos {
  from {
    background-position: top;
  }

  50% {
    background-position: bottom;
  }

  to {
    background-position: top;
  }
}

.my-works__img-wrap {
  width: 271px;
  height: 271px;
  margin-right: auto;
  margin-bottom: 1.18rem;
  margin-left: auto;

  background-color: $special-1;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  overflow: hidden;

  &--techno {
    background-image: url("../img/techno.jpg");
  }

  &--catenergy {
    background-image: url("../img/cat_mobile.jpg");
  }

  &--keksobooking {
    background-image: url("../img/keksobooking.jpg");
    background-size: 114%;
  }

  &--bigtrip {
    background-image: url("../img/bigtrip.jpg");
  }

  &--sixcities {
    background-image: url("../img/sixcities.jpg");
  }

  @media (min-width: $tablet-width) {
    &--catenergy {
      background-image: url("../img/cat_tablet.jpg");
    }
  }

  @media (min-width: $desktop-width) {
    width: 245px;
    height: 245px;
    margin-bottom: 1.2rem;

    &--catenergy {
      background-image: url("../img/cat_desktop.jpg");
    }
  }
}

.my-works__about {
  margin-bottom: 0.9rem;
  padding: 0.5rem;


  font-family: inherit;
  border: 1px solid $basic-text;
  background-color: transparent;

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.my-works__text-about {
  display: none;

  background-color: $basic-bg;
  opacity: 0.8;

  @media (min-width: $desktop-width) {
    position: absolute;
    top: 66%;
    right: -82%;
    left: 83%;
    z-index: 99;

    padding: 0.5rem;

    border: 2px solid $basic-text;
    opacity: 1;
  }
}

.my-works__decorate-code {
  bottom: -13rem;
  left: -20rem;

  width: auto;

  @media (min-width: $tablet-width) {
    bottom: -19rem;
    left: -35rem;
  }

  @media (min-width: $desktop-width) {
    bottom: -46rem;
    left: -58rem;
  }
}
